@font-face {
  font-family: LEngineer-Regular;
  src: url(/idealnemoloko-react/static/media/LEngineer-Regular.8a1b91a8.otf);
}
@font-face {
  font-family: Proxima-Nova-Semibold;
  src: url(/idealnemoloko-react/static/media/Proxima-Nova-Regular.410504d4.otf);
}
@font-face {
  font-family: Proxima-Nova-Bold;
  src: url(/idealnemoloko-react/static/media/Proxima-Nova-Bold.62d4d7d3.otf);
}

html {
  height: 100%;
  overflow-x: hidden;
  overflow-y: hidden;
}
body {
  height: 100%;
  margin: 0;
  padding: 0;
}
#root {
  height: 100%;
}
#root > div {
  height: 100%;
}

.splash-left {
  position: fixed;
  right: 81vw;
  bottom: -10%;
  height: 85%;
  z-index: 1;
}
.splash-right {
  position: fixed;
  right: 45vw;
  top: -38%;
  height: 80%;
  z-index: 1;
}

.left-bar {
  position: absolute;
  top: 5vh;
  right: 9.75vw;
  /* was 13vw */
  z-index: 2;

  font-family: Proxima-Nova-Bold;
  font-size: 1.8vh;
}
.left-bar > span {
  margin-right: 1.5vw;
}
.left-bar a {
  text-decoration: none;
  color: #ffffff;
}
.left-bar a:hover {
  color: #ffffff;
  /* text-shadow: 0px 0px 4px #555555cc; */
}

.social {
  position: absolute;
  top: 4.4vh;
  right: 4vw;
  z-index: 2;
}
.social a {
  display: inline-block;
  width: 3vh;
  height: 3vh;
  margin-right: 1vw;
}
.social img {
  width: 100%;
  height: 100%;
}
.lang {
  position: absolute;
  top: 5vh;
  right: 2vw;
  z-index: 2;

  font-family: Proxima-Nova-Bold;
  font-size: 1.8vh;
  color: #ffffff;
  cursor: pointer;
}

.main-page {
  height: 100%;
}

.logo {
  position: absolute;
  top: 5vh;
  left: 11vw;
  margin-left: -3vh;
  height: 14vh;
  z-index: 2;
  cursor: unset;
  opacity: 0;
}
.logo-not-main {
  opacity: 1;
}
.logo-hidden {
  opacity: 1;
  -webkit-animation-name: logo;
          animation-name: logo;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
}
@-webkit-keyframes logo {
  0%   {opacity: 0;}
  99.9%   {opacity: 0;}
  100%   {opacity: 1;}
}
@keyframes logo {
  0%   {opacity: 0;}
  99.9%   {opacity: 0;}
  100%   {opacity: 1;}
}

.loader {
  position: fixed;
  height: 100%;
  width: 100%;
  background: #bcffd0;
  opacity: 1;
  z-index: 17;
}
.loader-hidden {
  width: 0;
  height: 0;
  opacity: 0;
  -webkit-animation-name: loader;
          animation-name: loader;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
}
@-webkit-keyframes loader {
  0%   {opacity: 1; height: 100%; width: 100%}
  99.9%   {opacity: 0; height: 100%; width: 100%}
  100%   {opacity: 0; height: 0; width: 0}
}
@keyframes loader {
  0%   {opacity: 1; height: 100%; width: 100%}
  99.9%   {opacity: 0; height: 100%; width: 100%}
  100%   {opacity: 0; height: 0; width: 0}
}

.loader-img {
  position: absolute;

  top: 50%;
  left: 50%;
  margin-left: 0;
  height: 28vh;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 18;
}
.loader-img-hidden {
  top: 5vh;
  left: 11vw;
  margin-left: -3vh;
  height: 0;
  -webkit-transform: none;
          transform: none;

  -webkit-animation-name: loader-img;

          animation-name: loader-img;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
}
@-webkit-keyframes loader-img {
  0%   {top: 50%; left: 50%; margin-left: 0; height: 28vh;-webkit-transform: translate(-50%, -50%);transform: translate(-50%, -50%);}
  99.9%   {top: 5vh; left: 11vw; margin-left: -3vh; height: 14vh;-webkit-transform: none;transform: none;}
  100%   {top: 5vh; left: 11vw; margin-left: -3vh; height: 0;-webkit-transform: none;transform: none;}
}
@keyframes loader-img {
  0%   {top: 50%; left: 50%; margin-left: 0; height: 28vh;-webkit-transform: translate(-50%, -50%);transform: translate(-50%, -50%);}
  99.9%   {top: 5vh; left: 11vw; margin-left: -3vh; height: 14vh;-webkit-transform: none;transform: none;}
  100%   {top: 5vh; left: 11vw; margin-left: -3vh; height: 0;-webkit-transform: none;transform: none;}
}

.on-images-loaded, .on-images-loaded > div, .on-images-loaded > div > div {
  height: 100%;
}
.swiper-container, .swiper-wrapper, .swiper-slide {
  height: 100%;
  z-index: 0;
}

.swiper-slide-left {
  position: absolute;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  background-size: 65% 100%;
  background-position-x: -15%;
  background-repeat: no-repeat;
}

.swiper-slide-left > img {
  position: absolute;
  bottom: 5vh;
  right: 55vw;
  height: 25vh;
  width: 25vh;
  z-index: 1;
}

.swiper-slide-right {
  position: absolute;
  right: 0;
  height: 100%;
  width: 57%;
  box-sizing: border-box;
}

.swiper-slide-right-title {
  margin-top: 19vh;
  margin-left: 12vw;
  height: 5vh;

  text-align: center;
  font-family: LEngineer-Regular;
  font-size: 3.5vh;
  -webkit-font-smoothing: antialiased;
  color: #7d4526;
  cursor: pointer;
}
.swiper-slide-right-title-hidden {
  cursor: unset;
}

.swiper-slide-right-img {
  height: 70%;
  text-align: center;
  cursor: pointer;
}
.swiper-slide-right-img-hidden {
  cursor: unset;
}
.swiper-slide-right-img img {
  position: absolute;
  right: 43%;
  height: 70vh;

  -webkit-transform: translateX(50%);

          transform: translateX(50%);
}

